export const encode = string => {

    return string.replace(/(\w)\1+/g, (_,m, c) => {
        console.log(_);
        return `${m.length}${c}`;
    });
};

export const decode = string => {

    return string.replace(/(\d+)(\w)/g, (_,c,d)=> {
        console.log("the underscore: ",_);
        console.log("c: ",c);
        console.log("d: ",d);
        c.repeat(d);
    });
};

